<template>
  <div>
    <nav class="row navbar fixed-bottom navbar-expand-md d-lg-none bg-white text-secondary">
      <a @click="to('/')" class="btn">
          <i class="material-icons">home</i>
      </a>
      <a @click="to('/hits')" class="btn">
          <i class="material-icons">search</i>
      </a>
      <a @click="to('/chat')" class="btn">
          <i class="material-icons">chat</i>
      </a>
      <a @click="to('/notifications')" class="btn">
          <i class="material-icons">notifications</i>
      </a>
      <a @click="to('/@aribahtiar')" class="btn">
          <i class="material-icons">account_circle</i>
      </a>
   </nav>
</div>
</template>
<script>
export default {
  name: 'app',
  methods: {
    to (to) {
      this.$router.push(to)
    }
  }
}
</script>



