<template>
    <div>
      <div class="row">
        <div class="col-md-12 col-lg-3 d-none d-lg-block">
          <div class="col-md-12 col-lg-3 container position-fixed">
           <sidebar-left />
          </div>
        </div>
        <div class="col-md-12 col-lg-6">
            <h6 class="text-center pt-2"> Your Live </h6>
            <video :src="liveVideo.liveVideo.videoSrc" width="100%" controls autoplay>
                  Browser Not Support
            </video>
            <div class="row text-center">
              <div class="col">
                <a class="btn">
                    <i class="material-icons float-left pr-1">favorite_border</i>100 Like
                </a>
              </div>
              <div class="col ">
                <a class="btn">
                    <i class="material-icons float-left pr-1">remove_red_eye</i>20 Viewer
                </a>
              </div>
              <div class="col ">
                <a class="btn">
                    <i class="material-icons float-left pr-1">comment</i>100 Comments
                </a>
              </div>
            </div>
        </div>
        <div class="col-md-12 col-lg-3 d-none d-lg-block">
          <div class="col-md-12 col-lg-3 container position-fixed">
           <sidebar-left />
          </div>
        </div>
    </div>
    </div>
</template>
<script>
import sidebarLeft from '../components/sidebar-left'
import { mapState, mapMutations } from 'vuex'
export default {
  components: {
    'sidebar-left': sidebarLeft
  },
  computed: {
    ...mapState([
      'liveVideo'
    ])
  },
  mounted () {
    this.startVideo()
  },
  methods: {
    ...mapMutations([
      'setLiveVideo'
    ]),
    startVideo () {
      navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia || navigator.oGetUserMedia
      if (navigator.getUserMedia) {
        navigator.getUserMedia({ video: true }, this.handleVideo, this.errVideo)
      }
    },
    handleVideo (stream) {
      this.setLiveVideo({
        username: 'aribahtiar',
        videoSrc: window.URL.createObjectURL(stream)
      })
    },
    errVideo () {
    }
  }
}
</script>


