import Vue from 'vue'
import Router from 'vue-router'
import Timeline from '@/view/Timeline'
import Chatting from '@/view/Chatting'
import Live from '@/view/Live'
import LiveVideo from '@/view/LiveVideo'
import Profile from '@/view/Profile'
import auth from '@/view/auth'
import {store} from '../store'

Vue.use(Router)

const cekAuth = (to, from, next) => {
  if (store.getters.user) {
    next()
  } else {
    next('/biarhits.com/auth')
  }
}

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/biarhits.com/auth',
      name: 'auth',
      component: auth
    },
    {
      path: '/biarhits.com/',
      name: 'Timeline',
      component: Timeline,
      beforeEnter: cekAuth
    },
    {
      path: '/biarhits.com/@:username',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/biarhits.com/live',
      name: 'Live',
      component: Live
    },
    {
      path: '/biarhits.com/live/:user',
      name: 'LiveVideo',
      component: LiveVideo
    },
    {
      path: '/biarhits.com/chatting',
      name: 'Chatting',
      component: Chatting,
      beforeEnter: cekAuth
    }
  ]
})



