<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light text-secondary animated fadeIn">
      <div class="container">
          <a @click="to('/live')" class="btn animated infinite tada">
              <i class="material-icons ari-color-style">add_a_photo</i>
          </a>
          <a @click="to('/')" class="ari-color-style btn animated rubberBand text-primary">
            Biar Hits
          </a>
              <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="col-lg-8 navbar-nav mr-auto">
                  <li class="col-12 nav-item">
                    <div class="col-12 form-inline">
                      <input v-model="searchKey" class="col-8 form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search">
                      <button @click="search()" class="btn btn-outline-light text-secondary">
                        Search
                      </button>
                    </div>
                  </li>
                </ul>
                <div v-if="auth" class="row col-12">
                    <a @click="to('/hits')" class="btn btn-link">
                        <i class="material-icons float-left pr-1">trending_up</i> Hits
                    </a>
                    <a @click="to('/group')" class="btn btn-link">
                        <i class="material-icons">group</i>
                        <span class="badge position-absolute mt-2"></span>
                    </a>
                    <a @click="to('/chat')" class="btn btn-link">
                        <i class="material-icons">chat</i>
                        <span class="badge position-absolute mt-2"></span>
                    </a>
                     <a class="btn btn-link">
                        <i class="material-icons float-left animated infinite tada">notifications</i>
                        <span class="badge position-absolute mt-2 animated infinite tada">99+</span>
                    </a>
                    <a @click="to('/@aribahtiar')" class="btn btn-link">
                        <i class="material-icons">account_circle</i>
                    </a>
                    <a class="btn">
                        <i class="material-icons">apps</i>
                    </a>
                </div>
                <div v-if="!auth">
                  <a @click="to('/hits')" class="btn btn-link ari-color-style">
                      <i class="material-icons float-left pr-1">trending_up</i> Hits
                  </a>
                  <a @click="to('/signup')" class="btn btn-link">Sign Up</a>
                  <a @click="to('/login')" class="btn btn-link"> login</a>
                </div>
          </div>
      </div>
   </nav>
</div>
</template>
<style>
.ari-color-style{
  animation: color-style 5s linear 2s infinite alternate;  
}
@keyframes color-style {
    0% {color: #92a8d1}
    25% {color: #034f84}
    50% {color: #f7786b}
    75% {color: #618685}
    100% {color: #b1cbbb}
}
</style>
<script>
export default {
  name: 'app',
  props: ['auth'],
  data: () => {
    return {
      searchKey: ''
    }
  },
  created () {
  },
  methods: {
    to (to) {
      this.$router.push(to)
    },
    search () {
      this.to('search')
    }
  }
}
</script>


