<template>
      <div class="row">
       <div class="profile-cover col-md-12 text-white mb-4" style="background-image: url('static/img/posts/p11.jpg')">
          <div class="card-img-overlay">
            <h5 class="card-title">Card title</h5>
            <p class="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
            <p class="card-text">Last updated 3 mins ago</p>
          </div>
        </div>
        <div class="col-md-12 col-lg-3 d-none d-lg-block">
          <div class="container">
            Sidebar
          </div>
        </div>
        <div class="col-md-12 col-lg-5 animated zoomIn">

            <div class="row text-center">
              <div class="col">
                <a class="btn">List | Grid</a>
              </div>
              <div class="col ">
                <a class="btn">288 Follower</a>
              </div>
              <div class="col ">
                <a class="btn">Send Message</a>
              </div>
            </div>



            <div class="mb-4 box-shadow" v-for="post in posts">
              <div class="user-post">
                <b class="text-muted">
                  <img class="user-thum-post" :src="post.photo">
                  <span class="mt-4">{{ post.username }}</span>
                </b>
                <small class="float-right pointer">
                  <i class="material-icons text-secondary mr-2">more_vert</i>
                </small>
              </div>
              <div @dblclick="like(post)">
                <swiper v-if="post.imgslide" :options="postslide">
                  <swiper-slide v-for="slide in post.imgslide" :key="slide.src">
                    <img class="card-img-top pointer" :src="slide.src">
                  </swiper-slide>
                  <div class="swiper-pagination" slot="pagination"></div>
                </swiper>
              </div>
              <img @dblclick="like(post)" v-if="post.img && !post.imgslide" class="card-img-top pointer" :src="post.img" :alt="post.content">
                <p class="pt-2 pl-2">{{ post.content }}</p>
                <div class="d-flex justify-content-between align-items-center">
                  <div class="btn-group text-secondary">
                    <a v-if="post.islike" class="btn animated tada" @click="like(post)">
                      <i class="material-icons float-left pr-1">favorite</i>
                    {{ post.like }}</a>
                    <a v-if="!post.islike" class="btn animated infinite tada" @click="like(post)">
                      <i class="material-icons float-left pr-1">favorite_border</i>
                    {{ post.like }}</a>
                    <a class="btn"><i class="material-icons float-left pr-1">comment</i> {{ post.comments }}</a>
                  </div>
                  <a class="btn text-secondary">
                    <i class="material-icons float-left pr-1">share </i> {{ post.share }}
                  </a>
                </div>
                <a class="btn text-secondary pt-3">Load More...</a>
                <div class="comment mx-4">
                    <div class="media text-muted">
                      <p class="media-body pb-3 mb-0 small">
                        <b class="pointer text-gray-dark">
                          <img class="user-thum-message" :src="post.lastComment.photo"> {{ post.lastComment.username }}
                        </b>
                        {{ post.lastComment.comment }}
                      </p>
                    </div>
                </div>
            </div>
      </div>
      <div class="col-md-12 col-lg-3 d-none d-lg-block">
        <div class="container">
          Sidebar
        </div>
      </div>
    </div>
</template>
<script>
export default {
  data: () => {
    return {
      posts: {},
      postslide: {
        slideToClickedSlide: true,
        autoHeight: true,
        effect: 'fade',
        loop: true,
        autoplay: {
          delay: 3000
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  created () {
    this.posts = this.$store.getters.posts
  },
  methods: {
    Next () {
    },
    like (post) {
      if (!post.islike) {
        post.islike = true
        post.like++
      } else {
        post.islike = false
        post.like--
      }
    }
  },
  mounted () {
  }
}
</script>


