<template>
  <div id="app">
    <header class="bg-white fixed-top main-header">
      <topnav :auth="auth" />
      <nav-mobile />
    </header>
    <main>
      <router-view :auth="auth" class="animated fadeIn" style="animation-duration: 2s"></router-view>
    </main>
  </div>
</template>

<script>
import Topnav from './components/topnav'
import navMobile from './components/nav-mobile'

export default {
  name: 'app',
  data () {
    return {
      auth: this.$store.getters.user
    }
  },
  computed: {
    user () {
    }
  },
  created () {
  },
  watch: {
  },
  components: {
    'topnav': Topnav,
    'nav-mobile': navMobile
  }
}
</script>
<style>
.text-primary{
  color: #ccc
}
</style>


