import Vue from 'vue'
import Vuex from 'vuex'
import user from './user'
import timeline from './user/timeline'
import liveVideo from './liveVideo'

Vue.use(Vuex)

export const store = new Vuex.Store({
  modules: {
    timeline: timeline,
    user: user,
    liveVideo: liveVideo
  }
})



